@import '../../styles/vars.scss';
@import '../../styles/mixins.scss';

.eventServices {
  padding: 25px 0;
  
  font-family: $ff-tilda;
  
  background-color: $color-grey-light-2;
  
  @include vp-1260 {
    padding: 40px 0;
  }
}

.eventServices__titleWrapper {
  margin-bottom: 20px;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  text-align: center;
}

.eventServices__title {
  margin: 0;
  margin-bottom: 15px;
  
  font-size: calc($fz-m - 2px);
  font-weight: 500;
  
  @include vp-768 {
    font-size: $fz-m;
  }
}

.eventServices__subTitle {
  margin: 0;

  font-size: $fz-s;
}

.eventServices__content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  
  @include vp-768 {
    grid-template-columns: repeat(4, 1fr);
  }
}

.eventServices__itemLink {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: $base-dark-color;
  
  &:hover {
    color: $base-dark-color;
    
    & .eventServices__itemTextWrapper {
      height: 100%;
      transition: height 600ms ease-in-out;
    }
    
    & .eventServices__itemText {
      opacity: 1;
      transition-delay: 500ms;
    }

    & .eventServices__itemImage {
      transform: scale(1);
      transition-delay: 1s;
      transition: transform 500ms ease-out;
    }
  }
}

.eventServices__itemImageWrapper {
  margin-bottom: 5px;
  position: relative;

  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  overflow: hidden;
  border-radius: 10px;

  &div {
    width: 100% !important;
    height: 100% !important;
  
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  & img {
    aspect-ratio: 1/1;
    border-radius: 10px;

    object-fit: cover;
    
    transform: scale(1.05);
    transition: transform 500ms ease-in;
  }

  @include vp-1260 {
    margin-bottom: 10px;
  }
}

.eventServices__itemTitleWrapper {
  padding: 10px;

  width: 100%;

  position: absolute;
  bottom: 0;
  z-index: 11;

  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;

  background: linear-gradient(to bottom, transparent, rgba($base-dark-color, 0.8) 50%);

  @include vp-500 {
    padding: 20px;

    gap: 20px;
  }
}

.eventServices__itemTitle {
  margin: 0;
  
  font-size: $fz-s;
  font-weight: 500;
  color: $base-light-color;

  @include vp-500 {
    font-weight: 600;
    font-size: calc($fz-m - 3.5px);
  }
  
  @include vp-1260 {
    font-size: $fz-m;
  }
}

.eventServices__itemIcon {
  color: $base-light-color;
  font-size: $fz-m;
}

.eventServices__itemTextWrapper {
  padding: 20px;

  display: none;
  flex-direction: column;
  justify-content: center;
  gap: 20px;

  position: absolute;
  content: '';
  bottom: 0;
  left: 0;

  width: 100%;
  height: 0px;

  background-color: rgba($color-grey, 0.7);
  background: linear-gradient(to bottom, transparent, rgba($base-dark-color, 0.8) 50%);
  border-radius: 10px;

  transition: height 600ms ease-in-out;

  z-index: 10;

  @include vp-1260 {
    display: flex;
  }
}

.eventServices__itemText {
  margin: 0;

  font-size: calc($fz-m - 3px);
  line-height: 1;
  color: $base-light-color;

  opacity: 0;
  transition: all 350ms ease-in-out;
}
