@import '../../styles/styles.scss';
@import '../../styles/mixins.scss';

.gallery {
  padding: 20px 0;

  text-align: center;
  font-family: $ff-tilda;
  overflow: hidden;

  background-color: $base-light-color;

  & * {
    user-select: none;
  }

  @include vp-768 {
    padding: 20px 0 40px;
  }
  
  @include vp-1260 {
    padding: 40px 0;
  }
}

.gallery__container {
  @include vp-768 {
    padding-bottom: 20px;
  }
}

.gallery__title {
  margin: 0;
  margin-bottom: 40px;
  
  display: flex;
  align-items: center;
  gap: 10px;

  color: $base-dark-color;
  font-size: calc($fz-m - 2px);
  font-weight: 500;

  @include vp-768 {
    font-size: $fz-m;
  }

  @include vp-1260 {
    margin-bottom: 70px;
    justify-content: center;
  }
}

.gallery__showMoreLink {
  padding: 5px;

  width: 25px;
  height: 25px;

  display: flex;
  align-items: center;
  justify-content: center;

  font-size: $fz-s;
  color: $base-dark-color;

  background-color: $color-grey-light;
  border-radius: 50%;

  @include vp-1260 {
    display: none;
  }
}

.gallery__imageWrapper {
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  & div {
    width: 100% !important;
    height: 100% !important;

    display: flex;
    align-items: center;
    justify-content: center;
  
    @include vp-768 {
      max-width: 400px;
    }
  }

  & img {
    aspect-ratio: 1/1;
    border-radius: 8px;
    box-shadow: 0 2px 8px -4px $base-dark-color;
    object-fit: cover;
  }
}

// desktop only
.gallery__link {
  display: none;

  margin: 0 auto;
  padding: 13px 30px;

  max-width: 350px;

  font-family: $ff-tilda;
  font-size: $fz-s;
  font-weight: 500;
  color: $base-light-color;
  
  border-radius: 20px;
  background-color: $color-red;
  transition: all 0.2s ease-in-out;
  
  box-shadow: 
    -5px -5px 10px $base-light-color,
    0 3px 5px 0px rgba($base-dark-color, 0.3)
  ;

  &:hover {
    background-color: $color-red-light;
    color: $base-light-color;
  }

  @include vp-768 {
    margin: 0 auto;
  }
  
  @include vp-1260 {
    display: block;
    font-size: calc($fz-m - 3px);
  }
}

.gallery__content {
  margin-bottom: 20px;

  @include vp-1260 {
    margin-bottom: 50px;
  }
}

.gallery__lastItem {
  margin: auto;

  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery__lastItemTitle {
  margin: 0;
  margin-bottom: 10px;

  line-height: 1;
  
  @include vp-500 {
    margin-bottom: 20px;
  }
  
  @include vp-768 {
    margin-bottom: 40px;
    font-size: $fz-m;
  }
}

.gallery__lastItemLink {
  padding: 3px;
  
  background-color: $base-light-color;
  color: $base-dark-color;
  border-radius: 20px;
  box-shadow: 0 2px 5px 1px $color-grey;
  
  &:hover {
    color: $base-dark-color;
  }
  
  @include vp-500 {
    padding: 7px;
  }
  
  @include vp-768 {
    padding: 10px 20px;
  }
}
